import fetch from '../fetch'

/**
 * payitem service
 */
const Pays = {
  get: (id) => fetch({
    url: '/api/manage/payItem/get/' + id,
    method: 'get'
  }),
  list: () => fetch({
    url: '/api/manage/payItem/list',
    method: 'get'
  }),
  save: (data) => fetch({
    url: '/api/manage/payItem/save',
    method: 'post',
    data: data
  }),
};

export default Pays