import fetch from '../fetch'

/**
 * salary item service
 */
const Salary = {
  get: (id) => fetch({
    url: '/api/manage/salary/get/' + id,
    method: 'get'
  }),
  list: () => fetch({
    url: '/api/manage/salary/list',
    method: 'get'
  }),
  save: (data) => fetch({
    url: '/api/manage/salary/save',
    method: 'post',
    data: data
  }),
  delete: (data) => fetch({
    url: '/api/manage/salary/delete',
    method: 'post',
    data: data
  }),
  types: () => fetch({
    url: '/api/manage/salary/types',
    method: 'get',
  }),
  sort: (data) => fetch({
    url: '/api/manage/salary/sort',
    method: 'post',
    data: {'json_salary': data}
  }),
};

export default Salary