<template>
  <div class="tax-page tax-subject-cfg">
    <h4>薪酬项目<span>/</span>{{salary.id?'修改项目':'添加项目'}}</h4>
    <div class="content">
      <el-form ref="inputForm" :model="salary" :rules="rules" label-width="120px">
        <el-form-item label="薪酬项目" prop="name">
          <el-input v-model="salary.name" placeholder="请输入薪酬项目名称"></el-input>
          <div class="form-tip">请设置合理的项目名称，设置后不建议修改.</div>
        </el-form-item>
        <el-form-item label="项目类型" prop="salaryType">
          <el-select v-model="salary.salaryType" filterable placeholder="请选择" @change="changeType">
            <el-option v-for="item in types"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"/>
          </el-select>
          <div class="form-tip">项目类型说明这个项目的主要性质和数据来源.</div>
        </el-form-item>
        <el-form-item label="个税项目" prop="payItemName">
          <el-input v-model="salary.payItemName" placeholder="请输入选择个税项目" clearable @clear="deletePays">
            <template slot="append">
              <el-button type="primary" icon="el-icon-search" @click="selectPays"></el-button>
            </template>
          </el-input>
          <div class="form-tip">请选择对应的个税项目.</div>
        </el-form-item>
        <el-form-item label="薪酬项目枚举" prop="salarys">
          <el-input v-model="salary.salarys" type="textarea" rows="5" placeholder="请输入薪酬项目枚举"></el-input>
          <div class="form-tip">枚举同意义的薪酬项目，或者说明薪酬项目的意义.</div>
        </el-form-item>
        <el-form-item label="薪酬项目描述" prop="descs">
          <el-input v-model="salary.descs" type="textarea" rows="5" placeholder="请输入薪酬项目描述"></el-input>
          <div class="form-tip">薪酬项目描述, 描述此薪酬项目的意义.</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :loading="saveing" @click="save">提交</el-button>
          <swak-link class="el-button" to="/system/salaryitem">返回</swak-link>
        </el-form-item>
      </el-form>
    </div>
    
    <el-dialog
        title="选择个税项目"
        :visible.sync="dialogVisible"
        :before-close="addPayCancel">
      <el-table ref="paysGrid" :data="pays" border v-loading="loading" highlight-current-row
                @current-change="handlePayChange">
        <el-table-column property="name" label="个税项目名称"></el-table-column>
        <el-table-column property="includeTypeName" label="个税项目类型"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>

  import SalaryApi from '@/api/system/salary';
  import PayApi from '@/api/system/payItem';

  export default {
    components: {},
    name: "tax-subject-cfg",
    data() {
      return {
        salary: {},
        pays: [],
        types: [],
        dialogVisible: false,
        loading: false,
        rules: {
          name: [
            {required: true, message: '请输入项目名称', trigger: 'blur'},
          ],
          salaryType: [
            {required: true, message: '请输入项目类型', trigger: 'blur'},
          ]
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "salaryitem")
      var id = this.$route.params.id
      this.loadTypes()
      this.loadSalary(id)
    },
    methods: {
      loadTypes: function () {
        SalaryApi.types().then(res => {
          this.types = res.obj;
        });
      },
      changeType : function(row) {
      
      },
      loadSalary: function (id) {
        if (!id) {
          return;
        }
        SalaryApi.get(id).then(res => {
          this.salary = res.obj || {}
          this.salary.payItemName = this.salary.payItem ? this.salary.payItem.name: '';
        })
      },
      deletePays : function() {
        console.log(1)
        this.salary.payItemId = ''
        this.salary.payItemName = ''
      },
      selectPays: function () {
        this.dialogVisible = true;
        this.loading = true;
        PayApi.list().then(res => {
          this.pays = res.obj;
          this.loading = false;
        })
      },
      addPayCancel: function () {
        this.dialogVisible = false;
        this.loading = true;
      },
      handlePayChange: function (row) {
        if (row != null) {
          this.salary.payItemId = row.id;
          this.salary.payItemName = row.name;
          this.addPayCancel();
        }
      },
      save: function () {
        this.$refs['inputForm'].validate(res => {
          if (res) {
            this.saveing = true;
            SalaryApi.save(this.salary).then(res => {
              this.$router.replace("/system/salaryitem");
              this.$notify.success("保存项目成功!");
            });
          } else {
            this.$notify.error("表单错误");
          }
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
  .tax-subject-cfg {
    .types {
      a {
        display inline-block;
        padding 3px 10px;
        background-color $color-light-grey-s
        margin-right 10px;
      }
      .cur {
        background-color $color-success;
        color $color-white;
      }
    }
  }
  .delete-pays {
    color $color-primary;
    cursor: pointer;
  }
</style>